@font-face {
  font-family: quicksand;
  src:url("./res/Quicksand-VariableFont_wght.ttf")
}

@font-face {
  font-family: cinzel;
  src:url("./res/CinzelDecorative-Regular.ttf")
}

@font-face {
  font-family: prata;
  src:url("./res/Prata-Regular.ttf")
}


.banner {
  background-image: url("./res/banner_bg.jpeg");
  background-color: rgba(0,0,0,0.6);
  background-blend-mode: darken;
  background-size: cover;
  background-position: center;
  height: 60vh;
  width: 96vw;
  margin: 2vw;
  border-radius: 2vw;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.whyus_list li {
  margin:2vw;
  margin-top: 3vmax;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


.whyus_list_icn {
  color:rgb(2 34 128);
  font-size: 7vmax;
  
}

.LoginSignupElm {
  flex:1;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 2vmin;
}

.LoginSignupElm input {
  border:none;
  border-bottom: solid 2px grey;
  padding:2vh 1vw;
  padding-bottom: 5px;
  margin: 1vmax auto;
  text-align: center;
}

.LoginSignupElm input:focus {
  outline:none
}

.LoginSignupElm .act_button {
  background: #004cff;
  color:white;
  padding:1vmin 5vmin;
  border-radius: 1vmin;
  margin: 2vmax auto;
} 

.LoginSignupElm .chg_button {
  background: none;
  border: none;
  color:blue
}